import formInput from '~/components/formInput'

export default {
  scrollToTop: true,
  // transition: 'page',
  components: {
    formInput,
  },
  async asyncData({ $axios }) {
    const data = await $axios.$get('/friends')
    return { cardList: data.filter(v => v.isPass)}
  },
  data() {
    return {
      dialogVisible: false,
    };
  },
  head () {
    return {
      title: 'web圈',
      meta: [
        { hid: 'page', name: 'description', content: '这里是博客web圈, 想结交圈子里的各路英雄好友，一起成长！' }
      ]
    }
  },
  created() {
  },
  methods: {
    async getCardsList() {
      let data = await this.$axios.$get("/friends");
      this.cardList = data.filter(v => v.isPass);
    },
    async submit(form) {
      await this.$axios.$post("/friends", form);
      this.dialogVisible = false;
      this.$message({
        message: "提交成功，博主审核中。。。",
        type: "success",
        duration: 5000
      });
      this.getCardsList();
    },
    open(item) {
      window.open(item.url);
    }
  }
};
